import React, { useState, useEffect, useRef, useMemo, useCallback } from 'react';
import { 
  Layout, 
  Typography, 
  Button, 
  Card, 
  Table, 
  Space, 
  Tag, 
  ConfigProvider,
  Input,
  Switch,
  Avatar,
  Tooltip,
  Empty,
  Spin,
  Tabs,
  Radio,
  Row,
  Col
} from '@douyinfe/semi-ui';
import { 
  IconLink, 
  IconGithubLogo, 
  IconFile, 
  IconUpload, 
  IconCheckCircleStroked,
  IconSun, 
  IconMoon,
  IconSearch,
  IconCode,
  IconInfoCircle,
  IconBookStroked
} from '@douyinfe/semi-icons';
import bytedanceSecurityLogo from './logos/bytedance_security.png';
import hustLogo from './logos/hust.png';
import fudanLogo from './logos/fudan.png';
import PropTypes from 'prop-types';
import configData from './config.json';
import './App.css';

const { Content, Footer } = Layout;
const { TabPane } = Tabs;

// Import components
import AppHeader from './components/Header';
import ActionButtons from './components/ActionButtons';
import SearchBar from './components/SearchBar';
import ModelTable from './components/ModelTable';
import InfoCard from './components/InfoCard';
import NewsCard from './components/NewsCard';
import HuggingFaceIcon from './components/HuggingFaceIcon';

import en_US from '@douyinfe/semi-ui/lib/es/locale/source/en_US';

function App() {
  const [darkMode, setDarkMode] = useState(false);
  const [searchText, setSearchText] = useState('');
  const [activeConfig, setActiveConfig] = useState('default');
  const [scoreType, setScoreType] = useState('well');
  const [typeFilter, setTypeFilter] = useState('all');
  const contentRef = useRef(null);
  const leaderboardRef = useRef(null);
  const aboutRef = useRef(null);
  const citeRef = useRef(null);

  // Memoized values
  const currentConfig = useMemo(() => {
    return configData.evaluation_configs[activeConfig];
  }, [activeConfig]);

  const filteredAndSortedData = useMemo(() => {
    if (!currentConfig || !currentConfig.models) return [];
    
    let filtered = [...currentConfig.models];
    
    // Apply search filter
    if (searchText) {
      filtered = filtered.filter(item => 
        item.object_name.toLowerCase().includes(searchText.toLowerCase()) ||
        item.organization.toLowerCase().includes(searchText.toLowerCase())
      );
    }
    
    // Apply type filter
    if (typeFilter && typeFilter !== 'all') {
      filtered = filtered.filter(item => item.type === typeFilter);
    }
    
    // Apply score sorting
    const scoreKey = scoreType === 'resolved' ? 'score_resolved' : 'score_well';
    return filtered.sort((a, b) => b[scoreKey] - a[scoreKey]);
  }, [currentConfig, searchText, typeFilter, scoreType]);

  const citeBibtex = useMemo(() => {
    if (configData.content.cite.bibtex.template) {
      return configData.content.cite.bibtex.template;
    }
    // Fallback to old format if template doesn't exist
    return `@article{${configData.meta.title.toLowerCase().replace(/ /g, '-')}-${configData.meta.year},
  title={${configData.content.cite.bibtex.title}},
  author={${configData.content.cite.bibtex.author}},
  journal={${configData.content.cite.bibtex.journal}},
  year={${configData.meta.year}},
  url={${configData.links.code}}
}`;
  }, [configData]);

  // Effects
  useEffect(() => {
    document.body.setAttribute('theme-mode', darkMode ? 'dark' : 'light');
  }, [darkMode]);

  // Callbacks
  const handleThemeChange = useCallback((checked) => {
    setDarkMode(checked);
  }, []);

  const handleSearchChange = useCallback((value) => {
    setSearchText(value);
  }, []);

  const handleConfigChange = useCallback((configKey) => {
    setActiveConfig(configKey);
  }, []);

  const handleScoreTypeChange = useCallback((type) => {
    setScoreType(type);
  }, []);

  const handleTypeFilterChange = useCallback((filter) => {
    setTypeFilter(filter);
  }, []);

  const handleScrollTo = useCallback((ref) => {
    console.log('handleScrollTo called with ref:', ref);
    if (!ref || !ref.current) {
      console.warn('Scroll target ref is null or undefined');
      return;
    }

    try {
      const element = ref.current;
      console.log('Scrolling to element:', element);
      if (!element) return;

      // 
      const elementRect = element.getBoundingClientRect();
      const absoluteElementTop = elementRect.top + window.pageYOffset;
      const headerOffset = 80; // header + padding
      const scrollTop = absoluteElementTop - headerOffset;

      console.log('Scrolling to position:', scrollTop);
      window.scrollTo({
        top: scrollTop,
        behavior: 'smooth'
      });
    } catch (error) {
      console.error('Error scrolling to element:', error);
      // 
      window.scrollTo(0, 0);
    }
  }, []);

  const configTabs = useMemo(() => {
    return Object.entries(configData.evaluation_configs).map(([key, config]) => ({
      itemKey: key,
      tab: config.name,
      name: config.name,
      description: config.description
    }));
  }, []);

  return (
    <ConfigProvider theme={darkMode ? { mode: 'dark' } : { mode: 'light' }} locale={en_US}>
      <Layout className="app-layout">
        <AppHeader
          title={configData.meta.title}
          darkMode={darkMode}
          onThemeChange={handleThemeChange}
          onScrollTo={handleScrollTo}
          leaderboardRef={leaderboardRef}
          aboutRef={aboutRef}
          citeRef={citeRef}
        />

        <Content className="app-content" ref={contentRef}>
          <div className="content-wrapper">
            {/* Enhanced Action Buttons */}
            <Row justify="center" style={{ marginTop: -15, marginBottom: 23 }}>
              <Col>
                <Space size={16}>
                  <Button
                    type="primary"
                    size="large"
                    icon={<IconLink />}
                    onClick={() => window.open(configData.links.paper, '_blank')}
                    style={{
                      borderRadius: 12,
                      fontSize: 15,
                      padding: '10px 20px',
                      boxShadow: '0 2px 8px rgba(0,0,0,0.1)',
                      transition: 'all 0.2s ease',
                      fontWeight: 600
                    }}
                    className="enhanced-primary-button paper-button"
                  >
                    Paper
                  </Button>
                  <Button 
                    type="secondary" 
                    size="large" 
                    icon={<IconGithubLogo />}
                    onClick={() => window.open(configData.links.code, '_blank')}
                    style={{ 
                      borderRadius: 12,
                      fontSize: 15,
                      padding: '10px 20px',
                      boxShadow: '0 2px 8px rgba(0,0,0,0.1)'
                    }}
                  >
                    Code
                  </Button>
                  <Button 
                    type="secondary" 
                    size="large" 
                    icon={<HuggingFaceIcon />}
                    onClick={() => window.open(configData.links.data, '_blank')}
                    style={{ 
                      borderRadius: 12,
                      fontSize: 15,
                      padding: '10px 20px',
                      boxShadow: '0 2px 8px rgba(0,0,0,0.1)'
                    }}
                  >
                    Dataset
                  </Button>
                  <Button 
                    type="secondary" 
                    size="large" 
                    icon={<IconUpload />}
                    onClick={() => window.open(configData.links.submit, '_blank')}
                    style={{ 
                      borderRadius: 12,
                      fontSize: 15,
                      padding: '10px 20px',
                      boxShadow: '0 2px 8px rgba(0,0,0,0.1)'
                    }}
                  >
                    Submit
                  </Button>
                </Space>
              </Col>
            </Row>

            <Card className="leaderboard-card" ref={leaderboardRef} style={{ padding: '0px 0 0 0' }}>
              {/* Enhanced Configuration Tabs */}
              <div style={{
                marginTop: -35,
                marginBottom: 20,
                padding: '20px 0 15px 0',
                borderBottom: '1px solid var(--semi-color-border)'
              }}>
                <div style={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                marginBottom: 8
              }}>
                  <Tabs
                    type="line"
                    activeKey={activeConfig}
                    onChange={handleConfigChange}
                    style={{
                    marginBottom: 0,
                    flex: 1
                    }}
                  >
                    {configTabs.map(config => (
                      <TabPane
                        tab={
                          <div style={{
                            display: 'flex',
                            alignItems: 'center',
                            gap: 8,
                            fontSize: 20,
                            fontWeight: 600,
                            padding: '0px 0px 0px 0px'
                          }}>
                            <span>{config.name}</span>
                          </div>
                        }
                        itemKey={config.itemKey}
                        key={config.itemKey}
                      />
                    ))}
                  </Tabs>
                  <SearchBar
                    value={searchText}
                    onChange={handleSearchChange}
                    placeholder="Search models or organizations..."
                  />
              </div>
                <Typography.Text
                  type="secondary"
                  style={{
                    fontSize: 14,
                    lineHeight: 1.5,
                    display: 'block',
                    marginTop: 2,
                    color: 'var(--semi-color-text-1)'
                  }}
                >
                  {currentConfig?.description}
                </Typography.Text>
              </div>

  

              <div className="table-container">
                <ModelTable
                  data={filteredAndSortedData}
                  scoreType={scoreType}
                  onScoreTypeChange={handleScoreTypeChange}
                  typeFilter={typeFilter}
                  onTypeFilterChange={handleTypeFilterChange}
                />
              </div>
            </Card>

            <div style={{ marginTop: 24 }}>
              <NewsCard news={configData.news} />
            </div>

            <div style={{ marginTop: 24 }} ref={aboutRef}>
              <InfoCard
                type="about"
                content={{
                  ...configData.content.about,
                  title: `About ${configData.meta.title}`
                }}
                links={configData.links}
              />
            </div>

            <div style={{ marginTop: 24 }} ref={citeRef}>
              <InfoCard
                type="cite"
                content={{
                  ...configData.content.cite,
                  bibtex: citeBibtex
                }}
                links={configData.links}
              />
            </div>

            {/* Bottom spacing */}
            <div style={{ height: 40 }} />
          </div>
        </Content>

        <Footer className="app-footer" role="contentinfo">
          <Space vertical align="center" style={{ width: '100%' }}>
            {/* Logos Section */}
            <div style={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              gap: '32px',
              marginBottom: '16px'
            }}>
              <a
                href="https://joinbytedance.com/"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src={bytedanceSecurityLogo}
                  alt="ByteDance"
                  style={{
                    height: '37px',
                    width: 'auto',
                    objectFit: 'contain',
                    marginTop: '8px'
                  }}
                />
              </a>
              <a
                href="https://mingwen-cs.github.io/index.html"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src={hustLogo}
                  alt="HUST"
                  style={{
                    height: '47px',
                    width: 'auto',
                    objectFit: 'contain',
                    marginTop: '+8px'
                  }}
                />
              </a>
              <a
                href="https://yuanxzhang.github.io/"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src={fudanLogo}
                  alt="Fudan"
                  style={{
                    height: '45px',
                    width: 'auto',
                    objectFit: 'contain',
                    marginTop: '+5px'
                  }}  
              />
            </a>
            </div>

            <Typography.Text type="secondary">
               {configData.meta.year} {configData.meta.title}. All rights reserved.
            </Typography.Text>
          </Space>
        </Footer>
      </Layout>
    </ConfigProvider>
  );
}

export default App;
