import React from 'react';
import { Card, Typography, Space, Button } from '@douyinfe/semi-ui';
import { IconInfoCircle, IconBookStroked } from '@douyinfe/semi-icons';
import PropTypes from 'prop-types';

const { Title, Paragraph } = Typography;

const InfoCard = React.memo(({ type, content, links }) => {
  const isAbout = type === 'about';
  const Icon = isAbout ? IconInfoCircle : IconBookStroked;
  const color = isAbout ? 'var(--semi-color-primary)' : 'var(--semi-color-success)';

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(content.bibtex);
      // Could add toast notification here
    } catch (err) {
      console.error('Failed to copy:', err);
    }
  };

  const renderBibtexWithHighlight = (bibtexText) => {
    if (!bibtexText) return null;
    
    const lines = bibtexText.split('\n');
    return lines.map((line, index) => {
      // Special handling for @misc line
      const miscMatch = line.match(/^(@misc)(\{)([^,]+)(,)$/);
      if (miscMatch) {
        const [, miscType, openBrace, identifier, comma] = miscMatch;
        return (
          <div key={index} style={{ lineHeight: '1.4' }}>
            <span style={{ 
              color: 'var(--semi-color-warning)', 
              fontWeight: 700,
              fontFamily: 'monospace'
            }}>
              {miscType}
            </span>
            <span style={{ fontFamily: 'monospace' }}>{openBrace}</span>
            <span style={{ 
              color: 'var(--semi-color-success)', 
              fontWeight: 700,
              fontFamily: 'monospace'
            }}>
              {identifier}
            </span>
            <span style={{ fontFamily: 'monospace' }}>{comma}</span>
          </div>
        );
      }
      
      // Match lines with = sign and highlight the part before =
      // This regex captures: leading spaces + key + = + everything after
      const match = line.match(/^(\s*)([^=\s]+)(\s*=\s*)(.*)$/);
      if (match) {
        const [, leadingSpaces, key, equalsPart, value] = match;
        return (
          <div key={index} style={{ lineHeight: '1.4' }}>
            <span style={{ fontFamily: 'monospace' }}>{leadingSpaces}</span>
            <span style={{ 
              color: 'var(--semi-color-primary)', 
              fontWeight: 600,
              fontFamily: 'monospace'
            }}>
              {key}
            </span>
            <span style={{ fontFamily: 'monospace' }}>{equalsPart}{value}</span>
          </div>
        );
      }
      
      // For other lines without = sign
      return (
        <div key={index} style={{ 
          fontFamily: 'monospace',
          lineHeight: '1.4'
        }}>
          {line}
        </div>
      );
    });
  };

  return (
    <Card className={`${type}-card`}>
      <Space align="start" style={{ width: '100%' }}>
        <Icon style={{ fontSize: 24, color, flexShrink: 0, marginTop: 0 }} />
        <div style={{ flex: 1 }}>
          <Title heading={3} style={{ marginBottom: 16, marginTop: 0, fontSize: 18, fontWeight: 600 }}>
            {content.title}
          </Title>
          
          {isAbout ? (
            <>
              <Paragraph style={{ fontSize: '16px', lineHeight: '1.8' }}>{content.description}</Paragraph>
              {/* <Paragraph>{content.coverage}</Paragraph> */}
              {/* <Paragraph>
                <strong>{content.featuresTitle}</strong>
              </Paragraph>
              <ul style={{ marginLeft: 20, marginBottom: 16 }}>
                {content.features.map((feature, index) => (
                  <li key={index}>{feature}</li>
                ))} */}
              {/* </ul> */}
            </>
          ) : (
            <>
              <Paragraph style={{ fontSize: '16px', marginBottom: 20 }}>{content.description}</Paragraph>
              <Card style={{ backgroundColor: 'var(--semi-color-fill-0)', marginBottom: 16 }}>
                <div style={{ 
                  margin: 0, 
                  fontSize: 14, 
                  whiteSpace: 'pre-wrap',
                  fontFamily: 'monospace',
                  lineHeight: '1.4'
                }}>
                  {renderBibtexWithHighlight(content.bibtex)}
                </div>
              </Card>
              <Space>
                <Button 
                  type="primary" 
                  onClick={handleCopy}
                  aria-label="Copy citation to clipboard"
                >
                  {content.copyButton}
                </Button>
                <Button 
                  type="secondary" 
                  onClick={() => window.open(links.code, '_blank')}
                  aria-label="View GitHub repository"
                >
                  {content.githubButton}
                </Button>
              </Space>
            </>
          )}
        </div>
      </Space>
    </Card>
  );
});

InfoCard.displayName = 'InfoCard';
InfoCard.propTypes = {
  type: PropTypes.oneOf(['about', 'cite']).isRequired,
  content: PropTypes.shape({
    title: PropTypes.string.isRequired,
    description: PropTypes.string,
    coverage: PropTypes.string,
    featuresTitle: PropTypes.string,
    features: PropTypes.arrayOf(PropTypes.string),
    bibtex: PropTypes.string,
    copyButton: PropTypes.string,
    githubButton: PropTypes.string
  }).isRequired,
  links: PropTypes.shape({
    code: PropTypes.string
  }).isRequired
};

export default InfoCard;